import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { executeViewHooks } from '@angular/core/src/render3/instructions';

@Component({
  selector: 'app-task-dialog',
  templateUrl: './task-dialog.component.html',
  styleUrls: ['./task-dialog.component.css']
})
export class TaskDialogComponent implements OnInit {
  public active = false;
  operator: number;
  constructor() {
  }
  @Output() btnClickEvt = new EventEmitter<string>();
  ngOnInit() {
  }

  load(op: number) {
    this.operator = op;
    this.active = !this.active;
  }
  /**
   * 执行审批
   */
  submit() {
    // 默认是审批通过
    let executeType = 'Circulate';
    if (this.operator === 1) {// 审批不通过
      executeType = 'Terminate';
    }
    if (this.operator === 2) {// 驳回到上一级
      executeType = 'Reject';
    }
    this.btnClickEvt.emit(executeType);
    this.active = false;
  }

  cancel() {
    this.active = false;
  }
}



