import { Routes, RouterModule } from '@angular/router';
import { TaskHadDoneComponent } from './task-had-done/task-had-done.component';

const routes: Routes = [
    { path: '', component: TaskHadDoneComponent },
];

export const TaskHadDoneRoots = RouterModule.forChild(routes);



