import { Component, OnInit, Input, ComponentRef } from '@angular/core';
import { FrameworkService } from '@gsp-sys/rtf-common';
import { TaskListComponent } from '../../taskcenter/task-list/task-list.component';
import { TaskCenterService } from '../../taskcenter/taskcenter.service';
import { TaskList } from '../../taskcenter/taskList';


@Component({
  selector: 'app-had-list',
  templateUrl: './had-list.component.html',
  styleUrls: ['./had-list.component.css']
})
export class HadListComponent extends TaskListComponent {
  constructor(
    public taskService: TaskCenterService,
    protected frameworkSvc: FrameworkService,
    public taskList: TaskList
  ) {
    super(taskService, frameworkSvc, taskList);
  }
  // ngOnInit() {
  //   super.ngOnInit();
  // }

  /**
   * 获取绑定列表数据
   */
  protected getToDoList(): void {
    let filter = '';
    if (this.bizTypeId) {
      filter = `CategoryId eq '${this.bizTypeId}' and State eq 5`;
    } else {
      return;
    }
    this.taskService.fetchTaskList(this.skip, this.pageSize, filter)
      .subscribe(result => {
        // result.value.map((e) => this.taskService.toLowCase(e));
        this.todoList = result.value;
        this.gridView = {
          data: this.todoList,
          total: result['@odata.count']
        };
      });
  }
}



